<template>
  <div>
    <div>
    <van-nav-bar
    title="黑马程序员"
    left-text="返回"
    right-text="添加"
    left-arrow
    @click-left="onClickLeft"
    @click-right="onClickRight"/>
    </div>
    <div class="search">
      <form action="/">
  <van-search
    v-model="value"
    show-action
    placeholder="请输入搜索关键词"
    @search="onSearch"
    @cancel="onCancel"/>
</form>
    </div>
  <div v-show="searchStatus">
  <div class="searchText">
      <div>搜索历史</div>
      <div><van-icon name="delete" @click="delSearchSession" /></div>
  </div>
  <van-row>
    <van-col>
      <van-tag v-for="(item,index) in searchList" :key="index">{{item}}</van-tag>
    </van-col>
  </van-row>
</div>
<div v-show="!searchStatus">
  <van-swipe-cell v-for="item in list" :key="item.id">
  <van-cell :border="false" :title="item.name" :value="'上架时间：'+item.ctime.substring(0,10)" />
  <template #right>
    <van-button @click="delsearchOne(item)" square type="danger" text="删除" />
  </template>
</van-swipe-cell>
</div>
  <van-divider v-show="searchList.length==0">暂无搜索历史</van-divider>
  <g-tabbar />
  </div>
</template>

<script>
import GTabbar from '../tabbar'
export default {
  data () {
    return {
      value: '',
      searchList: [],
      searchStatus: true,
      list: []
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push('/home')
    },
    onClickRight () {
      console.log('2')
      this.$router.push('/Adds')
    },
    async onSearch () {
      this.searchStatus = false
      const data = await this.$http.get('/api/getprodlist')
      this.list = data.message

      const index = this.searchList.findIndex(item => {
        return item === this.value
      })
      if (index !== -1) return
      this.searchList.push(this.value)
      window.sessionStorage.setItem('searchText', this.searchList)
    },
    onCancel () {
      this.searchStatus = true
    },
    getSearchList () {
      window.sessionStorage.getItem('searchText') ? this.searchList = window.sessionStorage.getItem('searchText').split(',') : this.searchList = []
    },
    delSearchSession () {
      window.sessionStorage.clear()
      this.getSearchList()
    },
    delsearchOne (role) {
      console.log(role.id)
      this.$dialog.confirm({
        title: '删除商品',
        message: '确认要删除该商品吗？'
      }).then(async () => {
        console.log('点了确认')
        await this.$http.get(`/api/delproduct/${role.id}`)
        this.$notify({ type: 'success', message: '删除成功' })
        this.onSearch()
      }).catch(() => {
        this.$notify('取消了操作')
      })
    }
  },
  created () {
    this.getSearchList()
  },
  components: {
    GTabbar
  }
}
</script>

<style lang="less" scoped>
.search{
  margin-top:50px;
}
.searchText{
  font-size: 16px;
  display:flex;
  justify-content:space-between;
  padding:0px 20px;
  margin-top:20px;
}
.van-divider{
  margin-top:10px;
}
.van-row{
  padding:0px 10px;
}
.van-col{
  margin-top:10px;
}
.van-tag{
  margin-left:10px;
}
</style>
